import React, {KeyboardEvent} from "react";
import {DialogItem} from "./DialogItem/DialogItem";
import s from './Dialogs.module.css';
import {Message} from "./Message/Message";
import {ActionsTypes, DialogsPageType, DialogsType} from "../../redux/store";
import {addMessageActionCreator, onMessageChangeActionCreator} from "../../redux/dialogs-reducer";


type PropsType = {
    addMessage: () => void
    onMessageChange: (message: string) => void
    dialogs: DialogsPageType
}


export const Dialogs = (props: PropsType) => {


    let dialogsElement = props.dialogs.dialogs
        .map(d => <DialogItem name={d.name} id={d.id} img={d.img}/>);


    let messagesElement = props.dialogs.messages
        .map(m => <Message
            message={m.message}
            id={m.id}
        />)

    let newMessageElement = React.createRef<HTMLTextAreaElement>()

    const addMessage = () => {
        if (newMessageElement.current) {
            props.addMessage()

        }
    }
    const onKeyPressEnter = (e: KeyboardEvent<HTMLTextAreaElement>) => {
        if(e.key === 'Enter'){
            addMessage()
        }
    }

    let onMessageChange = () => {

        if (newMessageElement.current) {
            let message = newMessageElement.current.value
            props.onMessageChange(message)
        }
    }

    return (
        <div className={s.dialogs}>
            <div className={s.dialogsItems}>

                {dialogsElement}

            </div>
            <div className={s.messages}>

                <div className={s.addMessage}>
                    <button className={s.btnMessage} onClick={addMessage}>Send</button>
                    <textarea
                              onChange={onMessageChange}
                              className={s.areaMessage}
                              placeholder='message'
                              ref={newMessageElement}
                              onKeyPress={onKeyPressEnter}
                              value={props.dialogs.newMessageText}
                    />
                </div>
                {messagesElement}

            </div>
        </div>
    )
}